html {
  height: 100%;
}

body {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  display: flex;
}

.clock {
  background: #fff url("ios_clock.7c63ae86.svg") center / 88% no-repeat;
  border: 1px dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.clock:after {
  content: "";
  background: #ff0;
  border-radius: 50%;
  width: 5%;
  height: 5%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock__minutes-container, .clock__seconds-container {
  position: absolute;
  inset: 0;
}

.clock__minutes {
  transform-origin: 50% 100%;
  background: #00f;
  width: 2%;
  height: 20%;
  position: absolute;
  top: 30%;
  left: 49%;
}

.clock__minutes-container {
  animation: 3600s linear infinite move;
}

.clock__seconds {
  transform-origin: 50% 80%;
  background: green;
  width: 1%;
  height: 35%;
  position: absolute;
  top: 14%;
  left: 49.5%;
}

.clock__seconds-container {
  animation: 60s linear infinite move;
}

@keyframes move {
  from {
    transform: rotateZ(0);
  }

  to {
    transform: rotateZ(360deg);
  }
}

